<template>
<v-list-item dense>
    <v-row dense>
    <v-col cols="12" sm="4">
        <v-layout    align-center justify-center >
        <v-btn    color="primary" fab   small @click="add_filter" >
        <v-icon style="height:auto">add</v-icon>                  
        </v-btn>
    <v-overflow-btn
    v-model="selected_field"
    :items="fields"
    label="筛选字段"
    hint="选择筛选字段"
    dense
    >
    </v-overflow-btn>

    </v-layout>
    </v-col>

    
    
    <v-col cols="12" sm="3">
    <v-overflow-btn
    v-model="selected_type"
    :items="filter_types"
    label="类型"
    hint="选择筛选类型"
    dense
    >
    </v-overflow-btn>
    </v-col>

    <v-col cols="12" sm="3">
    <v-overflow-btn
    v-model="select_value"
    v-show="!input"
    editable
    :items="filter_values"
    label="值"
    hint="选择筛选值"
    dense
    >
    </v-overflow-btn>
   
    <v-text-field
    v-show="input"
    v-model="input_value"
    label="筛选值"
    >
    </v-text-field>
    </v-col>

    <v-col cols="12" sm="2">
    <v-layout row wrap  align-center justify-center >
        <v-switch
            v-model="input"
        ></v-switch>
        <v-btn    color="error" fab   small @click="delete_filter" >
        <v-icon style="height:auto">delete</v-icon>                  
        </v-btn>
    </v-layout>
    
 
    </v-col>
 
</v-row>
</v-list-item>
</template>

<script>
export default {
    props:{
        index:{
            type: Number,
            default:0,
        },//子组件在父组件的列表索引
        fields:{
            type:Array,
            default: function () {
                return []
            }
        },
        filter_types:{
            type:Array,
            default:function(){
                return [{text:"等于",value:""},{text:"大于",value:"$gt"},{text:"大于等于",value:"$gte"},{text:"小于",value:"$lt"},{text:"小于",value:"$lte"}]
            }
        },
        all_filter_values:{
            type:Object,
            default:function(){
                return{}
            }
        },
    },
    data:()=>({
        input: false,
        input_value:"",
        select_value:"",
        selected_field:"",
        selected_type:"",
    }),
    
    computed:{
        filter_values(){
            return this.all_filter_values[this.selected_field]
        }
    },
    methods:{
        // 发送结果到父组件
        send_filter_result(){
            if (this.selected_field){
                if (this.input){
                    // console.log("发送给父组件输入",this.selected_field,this.selected_type,this.input_value);
                    if (this.input_value){
                        this.$emit("get_filter_result",this.index,this.selected_field,this.selected_type,this.input_value)
                    }
                }else{
                    // console.log("发送给父组件选择",this.selected_field,this.selected_type,this.select_value);
                    if (this.select_value){
                        this.$emit("get_filter_result",this.index,this.selected_field,this.selected_type,this.select_value)
                    }
                    
                }
            }
        },
        // 通知父组件删除此筛选
        delete_filter(){
            this.$emit("delete_filter",this.index)
        },
        // 添加筛选
        add_filter(){
            this.$emit("add_filter")
        }
    },
    watch:{
        // 监听输入数据变化
        input_value: function(){
            this.send_filter_result();
        },
        // 监听选择数据变化
        select_value: function(){
            this.send_filter_result();
        },
        // 选择值
        selected_type:function(){
            this.send_filter_result();
        },
        // 选择字段
        selected_field: function(){
            if (this.selected_field){
                let tem=this.all_filter_values;
                if (!Object.prototype.hasOwnProperty.call(tem, this.selected_field)){
                    // 字段不存在 通知父组件更新
                    this.$emit("update_field_values",this.selected_field)
                }
            }
        }
    }
}
</script>